


body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    /* background-color: #FAFAFA; */
    font: 12pt "Tahoma";
}
* {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
}
.page {
    width: 215mm;
    min-height: 297mm;
    padding: 15mm;
    margin:  auto;
    border: 1px #D3D3D3 solid;
    border-radius: 5px;
    background: white;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}
.subpage {
    
    border: 2px black solid;
    height: 257mm;
    outline: 2cm white solid;

}

@page {
    size: A4;
    margin: 0;
}
@media print {
    html, body {
        width: 210mm;
        height: 297mm;        
    }
    .page {
        margin: 0;
        border: initial;
        border-radius: initial;
        width: initial;
        min-height: initial;
        box-shadow: initial;
        background: initial;
        page-break-after: always;
    }
}